"use client";

import Link from 'next/link';
import { usePathname } from 'next/navigation'; 
import MyProjects from '../myProjects/MyProjects';
import Image from 'next/image';
import NoticeBoard from '../../../public/assets/Notice Board Illustration.png';
import DashboardFalse from '../../../public/assets/Dashboard/Dashboard Icon False.png';
import DashboardTrue from '../../../public/assets/Dashboard/Dashboard icon.png';
import Tracker from '../../../public/assets/Dashboard/tracker icon.png';
import Pings from '../../../public/assets/Dashboard/ping icon.png';
import Hey from '../../../public/assets/Dashboard/Hey icon.png';
import Activity from '../../../public/assets/Dashboard/Activity icon.png';
import Mystuff from '../../../public/assets/Dashboard/my stuff icon.png';
import Logo from '../../../public/assets/Logo Icon.png';
import PingsTrue from '../../../public/assets/Dashboard/Pings True.png'
import HeyTrue from '../../../public/assets/Dashboard/Hey True.png';
import ActivityTrue from '../../../public/assets/Dashboard/Activity True.png';
import MystuffTrue from '../../../public/assets/Dashboard/My stuff True.png';
import { ChevronRight } from "lucide-react";

export default function Sidebar() {
    const pathname = usePathname();

    const menuItems = [
        {
            label: 'Dashboard',
            href: '/main/dashboard',
            icon: pathname === '/main/dashboard' ? DashboardTrue : DashboardFalse,
            isActive: pathname === '/main/dashboard',
        },
        {
            label: 'Pings',
            href: '/main/pings',
            icon: pathname=== 'main/pings' ? PingsFalse :Pings, 
            isActive: pathname === '/main/pings',
        },
        {
            label: 'Hey',
            href: '/main/hey',
            icon: Hey,
            isActive: pathname === '/main/hey',
        },
        {
            label: 'Activity',
            href: '/main/activity',
            icon: Activity,
            isActive: pathname === '/main/activity',
        },
        {
            label: 'My Stuff',
            href: '/main/my-stuff',
            icon: Mystuff,
            isActive: pathname === '/main/my-stuff',
        },
        {
            label: 'Tracker',
            href: '/main/tracker',
            icon: Tracker,
            isActive: pathname === '/main/tracker',
        },
    ];
    

    return (
        <aside className="bg-white min-h-screen p-3 text-sm">
   
    <div className="flex items-center gap-2 mb-4 mt-4">
        <div className="w-6 h-6 bg-[#5D56BD] rounded-md flex items-center justify-center">
            <Image src={Logo} alt="logo" width={18} height={18} />
        </div>
        <span className="text-gray-900 text-xs">
            <span className="font-bold text-lg">B</span>lackcofferToDos
        </span>
    </div>
    <nav className="space-y-1">
        {menuItems.map((item) => (
            <Link
                key={item.href}
                href={item.href}
                className={`flex items-center gap-2 py-1 relative rounded-md
                    ${item.isActive 
                        ? "text-[#565dbd] font-medium bg-[#565dbd]/10 before:absolute before:left-0 before:top-0 before:h-full before:w-1 before:bg-[#565dbd] before:rounded-r pl-3" 
                        : "text-[#4d4d4d] hover:bg-gray-50"
                    } transition-all duration-200 text-xs`}
            >
                <Image src={item.icon} alt="icon" width={18} height={18} />
                <span className="text-xs font-normal">{item.label}</span>
            </Link>
        ))}
    </nav>

    <hr className="my-3" />

    {/* Projects Section */}
    <div className="mt-4 max-h-[300px]">
        <div className="flex items-center justify-between mb-2 px-1 mx-auto">
            <h2 className="text-[14px] font-medium text-[#000000]">My Projects</h2>
            <Link href="/main/myprojects" className="flex items-center gap-1 text-[#5D56BD] text-[10px] hover:text-[#4A4495]">
                Show all
                <ChevronRight className="w-3 h-3" />
            </Link>
        </div>
        <MyProjects />
    </div>
</aside>

    );
}
